.App {
  text-align: center;
  background-color: #F2F2F2;
  min-height: 100vh;
  color: #121212;
}

.navbar {
  background-color: #121212;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 60px;
  position: fixed;
  top: 0;
  width: 100%;
  justify-content: space-between;
  z-index: 2;
  color: #F2F2F2;
}

.nav-items {
  text-align: right;
  margin-right: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

a {
  color: inherit;
  text-decoration: inherit;
}

.nav-items h4 {
  margin: 20px;
}

.th-head H1 {
  font-size: 55px;
  margin-bottom: 20px;
  margin-top: 140px;
}

.th-head p {
  color: #000000;
  margin-top: 0;
}

.discord {
  background-color: #5865F2;
  border-radius: 30px;
  width: 110px;
  height: 40px;
  display: flex;
  justify-content: center;

}

.discord h4 {
  padding-left: 14px;
  margin: 0px;
}

.discord-box {
  align-items: center;
  display: flex;
  flex-direction: row;
}

.App-header {
  margin: 0;
}

.App-link {
  color: #61dafb;
}

.logo {
  height: 55px;
}

.Home-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 70vh;
  margin-left: 10px;
  margin-right: 10px;
  padding-top: 30px;
}

.footer {
  padding-top: 20px;
  background-color: #121212;
  min-height: 20vh;
  color: white;
  fill: white;
}

.row-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 35px;
  padding-left: 35px;
  align-items: center;
}

.footer img {
  height: 75px;
  padding-top: 30px;
}

.footer hr {
  margin-left: 35px;
  margin-right: 35px;
}

.socials svg {
  padding-left: 20px;
  align-items: center;
  fill: wheat;
}

.bg-img {
  width: 100%;
  z-index: 1;
  position: relative;
  padding-top: 21px;
}

.overlay {
  width: 100%;
  margin-top: -525px;
}

.col {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
}

.bg_about {
  width: 65%;
  padding-top: 65px;
}

.col h2 {
  justify-content: left;
  padding-right: 30px;
}

.team-img {
  height: 140px;

}

.sp-ev {
  justify-content: space-evenly;
}

.pd-lr-20 {
  padding: 0 20px 0 20px;
}

.mt-0 {
  margin-top: 0;
}

.mb-0 {
  margin-bottom: 0;
}

.bold {
  font-weight: bold;
}

.fw-500 {
  font-weight: 500;
}

.fs-17 {
  font-size: 17px;
}

.ev-card {
  max-width: 400px;
  background-color: #D9D9D9;
  margin: 20px;
  border-radius: 30px;
  padding: 45px 0 30px 0;

}

.ev-img {
  width: 300px;
  /* padding-top: 30px; */
}

.al-lf {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.pd-lr-20 {
  padding: 0 20px 0 20px;
}

.text-a-l{
  text-align: start;
}